html {
  position: relative;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  min-width: 320px;
  min-height: 100vh;
  font-family: Arial, sans-serif;
}

img,
svg {
  max-width: 100%;
  max-height: 100%;
  height: auto;
}

button {
  padding: 0;
  cursor: pointer;
}

a {
  text-decoration: none;
  color: inherit;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

h1,
h2,
h3,
p,
figure,
fieldset {
  margin: 0;
}

/*
Доступно скрываю элементы
*/
.visually-hidden {
  position: fixed;
  transform: scale(0);
}

html {
  scroll-behavior: smooth;
}

body {
  font-family: Roboto, sans-serif;
  font-weight: 400;
  line-height: normal;
  color: #212529;
}

.body__wrapper {
  overflow: hidden;
  width: 100%;
  position: relative;
}

.container {
  max-width: 1440px;
  margin: 0 auto;
  padding: 0 32px;
}

@media (max-width: 1024px) {
  .container {
    padding: 0 24px;
  }
}
@media (max-width: 600px) {
  .container {
    padding: 0 16px;
  }
}
.header {
  margin-bottom: 40px;
}

.header__container {
  display: flex;
  justify-content: space-between;
  position: relative;
  padding-top: 12px;
}

.header__wrapper {
  display: flex;
  gap: 207px;
}

.header__item:not(:last-child) {
  margin-bottom: 8px;
}

.header__wrapper {
  font-size: 18px;
  line-height: 21px;
}

.header__burger-btn {
  border: none;
  display: none;
}

.header__burger-btn span {
  position: absolute;
  width: 24px;
  height: 1px;
  background-color: #000000;
  right: 16px;
  transition: transform 0.3s, background-color 0.5s;
}

.header__burger-btn span:nth-child(1) {
  transform: translateY(-6px);
}

.header__burger-btn span:nth-child(2) {
  transform: translateY(6px);
  width: 14px;
}

.header.open .header__burger-btn span {
  background-color: #000000;
}

.header.open .header__burger-btn span:nth-child(1) {
  transform: translateY(0) rotate(45deg);
}

.header.open .header__burger-btn span:nth-child(2) {
  transform: translateY(0) rotate(-45deg);
  width: 24px;
}

.header.open .header__burger-btn span:nth-child(3) {
  display: none;
}

@media (max-width: 1024px) {
  .header__wrapper {
    gap: 100px;
  }
  .header__item:not(:last-child) {
    margin-bottom: 4px;
  }
  .header__wrapper {
    font-size: 16px;
    line-height: normal;
  }
}
@media (max-width: 600px) {
  .header {
    margin-bottom: 25px;
  }
  .header__container {
    position: relative;
  }
  .logo {
    width: 26px;
    height: 28px;
  }
  .header__wrapper {
    display: block;
    position: absolute;
    width: 350px;
    height: 300px;
    top: 0;
    right: -350px;
    color: #fff;
    font-size: 24px;
    background-color: blueviolet;
    visibility: hidden;
    transition: transform 0.5s, visibility 0.5s;
    z-index: 5;
    padding: 50px 20px 20px 50px;
  }
  .header__item:not(:last-child) {
    margin-bottom: 15px;
  }
  .header__item:last-child {
    margin-bottom: 15px;
  }
  .header__burger-btn {
    display: block;
    z-index: 10;
  }
  .header.open .header__burger-btn span {
    background-color: #fff;
  }
  .header.open .header__wrapper {
    transform: translateX(-100%);
    visibility: visible;
  }
}
.home {
  position: relative;
  height: 1107px;
  margin-bottom: 120px;
}

.home__grid {
  display: grid;
  grid-template-columns: 700px 163px;
  grid-template-rows: 258px 84px;
  justify-content: space-between;
  align-items: end;
  gap: 40px;
  grid-row-end: auto;
}

.home__title {
  grid-column: 1/2;
  grid-row: 1/2;
  width: 700px;
  font-family: Helvetica;
  font-size: 72px;
  font-weight: 400;
  line-height: 86px;
  text-transform: uppercase;
}

.home__btn {
  grid-column: 2/3;
  grid-row: 1/2;
  font-size: 18px;
  line-height: 21px;
  text-transform: uppercase;
  background-color: white;
  border: none;
  border-bottom: solid 1px #212529;
}

.home__text {
  grid-column: 1/2;
  grid-row: 2/3;
  width: 410px;
  font-size: 20px;
  line-height: 28px;
}

.home__img1 {
  position: absolute;
  right: 32px;
  top: 298px;
}

.home__img2 {
  position: absolute;
  bottom: 0;
}

@media (max-width: 1190px) {
  .home {
    height: 1008px;
  }
  .home__img1 {
    width: 50%;
  }
}
@media (max-width: 1024px) {
  .home {
    margin-bottom: 70px;
  }
  .home__grid {
    grid-template-columns: 510px 145px;
    gap: 30px;
  }
  .home__title {
    width: 510px;
    font-size: 52px;
    line-height: 62px;
  }
  .home__btn {
    font-size: 16px;
    line-height: 19px;
  }
  .home__text {
    width: 370px;
    font-size: 18px;
    line-height: 25px;
  }
  .home__img1 {
    right: 24px;
  }
}
@media (max-width: 1024px) {
  .home__img1 {
    width: 41%;
  }
}
@media (max-width: 735px) {
  .home {
    height: auto;
    margin-bottom: 48px;
  }
  .home__grid {
    grid-template-columns: 1fr;
    grid-template-rows: 114px 66px 21px;
    gap: 20px;
    margin-bottom: 24px;
  }
  .home__title {
    width: 311px;
    font-size: 32px;
    line-height: 38px;
  }
  .home__btn {
    width: 164px;
    grid-column: 1/1;
    grid-row: 3/4;
    font-size: 18px;
    line-height: 21px;
  }
  .home__text {
    grid-column: 1/1;
    grid-row: 2/3;
    width: 330px;
    font-size: 16px;
    line-height: 22px;
  }
  .home__img1 {
    position: static;
    max-width: 100%;
    width: -webkit-fill-available;
    width: -moz-available;
    top: 275px;
    right: 16px;
    left: 16px;
  }
  .home__img2 {
    display: none;
  }
}
